 [class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: never;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
 
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
 
.icon-noun_buffer:before { content: '\E800'; } /* '' */
.icon-noun_incoming:before { content: '\E801'; } /* '' */
.icon-noun_history:before { content: '\E802'; } /* '' */
.icon-noun_proposed:before { content: '\E803'; } /* '' */
.icon-bug:before { content: '\F188'; } /* '' */
body{font-family:'overpass';font-weight:800
}
a{color:#ec5800
}
a:hover{color:#af4100
}
.ebl-kick{left:90%
}
.is-disabled{color:grey;cursor:not-allowed
}
.has-text-danger{color:#6f1a07;font-weight:800
}
.has-text-info{color:#247ba0
}
.ebl-nav-on{color:#ec5800
}
.ebl-nav-on:hover{color:#af4100
}
.ebl-footer-on{color:#330f0a
}
.ebl-footer-on:hover{color:#661e14
}
.ebl-bg-within{background-color:#4b5320
}
.ebl-bg-within a{color:#e9e9e9
}
.ebl-bg-within a:hover{color:#ec5800
}
.ebl-bg-without{background-color:#6f1a07
}
.ebl-bg-without a{color:#e9e9e9
}
.ebl-bg-without a:hover{color:#e9e9e9
}
#map{position:absolute;top:0;height:100%;width:100%;z-index:0
}
#vap{position:relative;top:0;left:0;width:100%;height:100vh;width:100%
}
#vap-copy{height:80px
}
#ebl-modal-dropzone .modal-background{background-color:#f4fdaf;color:#330f0a
}
.ebl-button{color:#ec5800
}
.ebl-button:hover{color:#af4100
}
.ebl-panel{width:33%;position:relative;right:0;background-color:rgba(255,255,255,0.8);padding:2em 1em
}
.navbar-item{color:#330f0a
}
.footer{z-index:7;width:100%;position:absolute;bottom:0;left:0;height:8vh;margin:0
}
.footer #konsole{height:4vh
}
.footer #ebl-layers{height:4vh
}
#modal-credits{color:white;font-weight:800
}
@keyframes fade{
from{opacity:1
}
to{opacity:0
}
}
.faded0{animation-name:fade;animation-fill-mode:forwards;animation-duration:1s;animation-iteration-count:1
}
.faded12{animation-name:fade;animation-fill-mode:forwards;animation-duration:12s;animation-iteration-count:1
}
.faded20{animation-name:fade;animation-fill-mode:forwards;animation-duration:20s;animation-iteration-count:1
}

.leaflet-vertex-icon{border-radius:50%
}
.leaflet-vertex-icon-hidden{display:none
}
